<template>
  <div>
    <v-card flat>
      <v-layout row wrap :class="`pa-3 category ${item.status}`">
        <v-flex xs2 md2>
          <div class="category grey--text ml-2">{{ item.name }}</div>
        </v-flex>
        <v-flex xs2 md2>
          <div class="category grey--text">{{ item.description }}</div>
        </v-flex>
        <v-flex xs2 md2>
          <div class="category grey--text">{{ item.slug }}</div>
        </v-flex>
        <v-flex xs8 md2>
          <v-chip
            label
            v-if="item.status === true"
            color="success"
            small
            @click="doUpdateStatus(item)"
          >
            <v-avatar left>
              <v-icon>done</v-icon>
            </v-avatar>
            Active
          </v-chip>
          <v-chip
            label
            small
            v-else
            color="red white--text"
            @click="doUpdateStatus(item)"
          >
            <v-avatar left>
              <v-icon>clear</v-icon>
            </v-avatar>
            Unactive
          </v-chip>
        </v-flex>

        <v-flex xs4 md4>
          <div class="category white--text">
            <v-btn
              class="red mr-1 mb-1"
              small
              @click="doDeleteCategory(item.id)"
            >
              <v-icon small>delete</v-icon> Delete
            </v-btn>

            <v-btn class="primary mr-1 mb-1" small @click.stop="update(item)">
              <v-icon small>system_update_alt</v-icon> Update
            </v-btn>
            <v-dialog
              max-width="600px"
              v-model="dialog"
              transition="fab-transition"
              :retain-focus="false"
            >
              <v-card>
                <v-card-title>
                  <h2>Edit Category Form</h2>
                </v-card-title>
                <v-card-text>
                  <v-form class="px-2" ref="form" @submit.prevent="doUpdate">
                    <v-text-field
                      label="Name"
                      :rules="nameRules"
                      v-model="name"
                    ></v-text-field>
                    <v-textarea
                      label="Description"
                      :rules="descriptionRules"
                      v-model="description"
                    ></v-textarea>
                    <v-text-field
                      label="Slug"
                      :rules="slugRules"
                      v-model="slug"
                    ></v-text-field>

                    <v-btn color="success" type="submit" :loading="loading"
                      >Save</v-btn
                    >
                  </v-form>
                </v-card-text>
              </v-card>
            </v-dialog>
          </div>
        </v-flex>
      </v-layout>
      <v-divider></v-divider>
    </v-card>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      dialog: false,
      id: "",
      name: "",
      description: "",
      slug: "",
      loading: false,
      nameRules: [
        (v) => !!v || "Name is required",
        (v) => (v && v.length <= 10) || "Name must be less than 10 characters",
      ],
      descriptionRules: [
        (v) => !!v || "Description is required",
        (v) =>
          (v && v.length <= 100) ||
          "Description must be less than 10 characters",
      ],
      slugRules: [
        (v) => !!v || "Slug is required",
        (v) => (v && v.length <= 10) || "Slug must be less than 10 characters",
      ],
    };
  },

  props: ["item"],
  computed: {
    ...mapState("category", ["category"]),
  },
  methods: {
    ...mapActions("category", ["updateStatus", "doUpdateCategory"]),

    doDeleteCategory(id) {
      let notify = {
        color: "error",
        text: "Are you sure ?",
        id: id,
      };
      this.$emit("Notify", notify);
    },
    doUpdateStatus(item) {
      this.updateStatus(item);
      let notify = {
        color: "info",
        text: `${item.name} Update Status Success !!!`,
      };
      this.$emit("Notify", notify);
    },
    async update(item) {
      this.dialog = true;
      this.name = item.name;
      this.description = item.description;
      this.slug = item.slug;
      this.id = item.id;
    },
    async doUpdate() {
      let dataCategory = [
        { categoryId: this.id },
        { propName: "name", value: this.name },
        { propName: "description", value: this.description },
        { propName: "slug", value: this.slug },
      ];
      let name = this.category.find((i) => {
        return i.name == this.name;
      });

      if (name) {
        let notify = {
          color: "error",
          text: "Category is exits !!!",
        };
        this.$emit("Notify", notify);
      } else {
        this.loading = true;
        this.doUpdateCategory(dataCategory);
        this.dialog = false;
        let notify = {
          color: "success",
          text: "Category update success !!!",
        };
        this.$emit("Notify", notify);
      }
      this.loading = false;
    },
  },
};
</script>

<style scoped>
.true {
  border-left: 4px solid green;
}
.false {
  border-left: 4px solid red;
}
</style>
