<template>
  <v-container class="my-5">
    <v-layout row wrap align-center justify-space-around class="lighten-2" flat>
      <span class="mr-3 "><v-icon>dashboard</v-icon> Toolbar</span>
      <v-flex md2>
        <v-btn color="success" small @click.stop="dialog = true"
          ><v-icon>add</v-icon> Add New</v-btn
        >
        <v-dialog
          max-width="600px"
          v-model="dialog"
          transition="fab-transition"
        >
          <v-card>
            <v-card-title>
              <h2>Add a New Category</h2>
            </v-card-title>
            <v-card-text>
              <v-form class="px-2" ref="form">
                <v-text-field
                  label="Name"
                  :rules="nameRules"
                  v-model="name"
                ></v-text-field>
                <v-textarea
                  label="Description"
                  :rules="descriptionRules"
                  v-model="description"
                ></v-textarea>
                <v-text-field
                  label="Slug"
                  :rules="slugRules"
                  v-model="slug"
                ></v-text-field>
                <v-select
                  :items="items"
                  :rules="[(v) => !!v || 'Status is required']"
                  v-model="status"
                  label="Status"
                ></v-select>
                <v-btn
                  color="success"
                  type="submit"
                  @click.prevent="add"
                  :loading="loading"
                  >Add</v-btn
                >
              </v-form>
            </v-card-text>
          </v-card>
        </v-dialog>
      </v-flex>
      <v-flex md2>
        <!-- <input
          type="file"
          name="image"
          hidden
          ref="fileInput"
          accept="image/*"
          @change="onFilePicked"
        /> -->
        <v-file-input
          accept="image/*"
          color="primary"
          small
          v-model="image"
          @change="onPickFile"
        ></v-file-input>
      </v-flex>
      <v-flex md2 v-show="image">
        <img width="50px" :src="imageUrl" alt="" />
      </v-flex>
      <v-flex md2>
        <v-btn color="info" small
          ><v-icon>vertical_align_top</v-icon>Export Exel</v-btn
        >
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      dialog: false,
      imageUrl: "",
      image: null,
      items: ["Active", "Unactive"],
      name: "",
      description: "",
      slug: "",
      status: null,
      loading: false,
      nameRules: [
        (v) => !!v || "Name is required",
        (v) => (v && v.length <= 25) || "Name must be less than 10 characters",
      ],
      descriptionRules: [
        (v) => !!v || "Description is required",
        (v) =>
          (v && v.length <= 100) ||
          "Description must be less than 10 characters",
      ],
      slugRules: [
        (v) => !!v || "Slug is required",
        (v) => (v && v.length <= 10) || "Slug must be less than 10 characters",
      ],
    };
  },
  components: {},

  computed: {
    ...mapState("category", ["category"]),
  },
  methods: {
    onPickFile() {
      const files = this.image;
      console.log(files);
      if (files) {
        const fileReader = new FileReader();
        fileReader.readAsDataURL(files);
        fileReader.addEventListener("load", () => {
          this.imageUrl = fileReader.result;
        });
      }
    },
    ...mapActions("category", ["addCategory", "getListCategory"]),
    add() {
      if (this.$refs.form.validate()) {
        let newCategory = {
          name: this.name,
          description: this.description,
          slug: this.slug,
          status: this.status,
        };
        const data = this.category.find((i) => {
          return i.name == newCategory.name;
        });
        if (data) {
          let notify = {
            color: "error",
            text: "Category exits !!!",
          };
          this.$emit("Notify", notify);
        } else {
          let notify = {
            color: "success",
            text: "Category create success !!!",
          };
          this.$emit("Notify", notify);
          this.loading = true;
          this.addCategory(newCategory);
          this.$refs.form.reset();
          this.dialog = false;
        }
      }
      this.loading = false;
    },
  },
};
</script>

<style></style>
