<template>
  <div id="category">
    <h2 class="subheading">Category</h2>
    <v-snackbar
      v-model="snackbar"
      :timeout="action ? 100000 : 3000"
      top
      right
      :color="color"
    >
      <span>{{ text }}</span>
      <v-btn v-if="action" text @click="doDelete(action)">Yes</v-btn>
      <v-btn
        text
        color="white"
        @click="snackbar = false"
        v-if="action ? (textBtn = 'No') : (textBtn = 'Close')"
        >{{ textBtn }}</v-btn
      >
    </v-snackbar>
    <NavbarCategory @Notify="notifi" />
    <v-container class="my-5">
      <CategoryCard
        @Notify="notifi"
        v-for="item in category"
        :key="item.id"
        :item="item"
      />
    </v-container>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import CategoryCard from "@/components/Category/CategoryCard.vue";
import NavbarCategory from "@/components/Category/NavbarCategory.vue";
export default {
  data() {
    return {
      snackbar: false,
      color: "",
      text: "",
      action: "",
      textBtn: "",
    };
  },
  components: {
    NavbarCategory,
    CategoryCard,
  },
  created() {
    this.getListCategory();
  },

  computed: {
    ...mapState("category", ["category"]),
  },
  methods: {
    ...mapActions("category", ["getListCategory", "deleteCategory"]),
    ...mapActions("product", ["deleteProductInCate"]),
    notifi(data) {
      this.snackbar = true;
      this.text = data.text;
      this.color = data.color;
      this.action = data.id;
    },
    doDelete(id) {
      this.deleteProductInCate(id);
      this.deleteCategory(id);
      this.snackbar = false;
    },
  },
};
</script>

<style scoped></style>
